// app/admin/chat/page.tsx
'use client';
import React, { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/navigation';
import { Button } from '@/app/components/ui/button';
import { Input } from '@/app/components/ui/input';
import { ScrollArea } from '@/app/components/ui/scroll-area';
import { Alert, AlertDescription } from '@/app/components/ui/alert';
import { 
  MessageCircle, 
  Send, 
  Loader2, 
  User, 
  Sparkles,
  Calendar, 
  Clock,
  MapPin,
  Lightbulb,
  DownloadCloud,
  RefreshCw
} from 'lucide-react';

interface Message {
  id: string;
  content: string;
  type: 'user' | 'ai';
  timestamp: Date;
  isLoading?: boolean;
}

interface BirthDetails {
  date: string;
  time: string;
  place: string;
  latitude?: string;
  longitude?: string;
}

// Initial welcome message
const WELCOME_MESSAGE = "Welcome to your Parasara Jyotish astrological consultation. I'm your AI astrologer, using Vedic astrology principles to provide insights based on your birth chart. How can I assist you today?";

// Fallback responses for when the backend doesn't respond properly
const FALLBACK_RESPONSES = [
  "Based on your birth chart, I can see significant planetary influences affecting your current situation. The positioning of Saturn suggests a period of growth through challenges.",
  "Your birth chart shows an interesting alignment between Jupiter and Mars, which typically indicates good opportunities for personal and professional development in the coming months.",
  "Looking at your birth details, the Moon's position in your chart suggests you have deep intuitive abilities. This is a good time to trust your instincts on important decisions.",
  "The position of Venus in your birth chart indicates a harmonious period for relationships. This is a good time to strengthen existing bonds or form new meaningful connections.",
  "According to your birth chart, Mercury's influence suggests this is an excellent time for communication and learning new skills. Consider pursuing education or training opportunities.",
  "Your astrological chart shows a strong Sun placement, indicating natural leadership abilities. You might find yourself taking charge in situations where direction is needed."
];

// Get a random fallback response
const getRandomResponse = (query: string) => {
  // Simple keyword matching for more relevant responses
  if (query.toLowerCase().includes('career') || query.toLowerCase().includes('job') || query.toLowerCase().includes('work')) {
    return "Based on your birth chart, your 10th house shows promising career growth potential. Your natural talents align well with leadership or specialized technical roles. The coming months look particularly favorable for career advancement.";
  }
  
  if (query.toLowerCase().includes('love') || query.toLowerCase().includes('relationship') || query.toLowerCase().includes('marriage')) {
    return "Looking at your Venus placement in your birth chart, I can see you have a deep capacity for meaningful relationships. Your 7th house indicates you seek balance and harmony in partnerships. The next few months show favorable aspects for strengthening existing bonds.";
  }
  
  if (query.toLowerCase().includes('health') || query.toLowerCase().includes('wellness')) {
    return "Your birth chart indicates a strong vitality from your Sun position, but Mars suggests you might need to be mindful of stress. Balancing physical activity with adequate rest would be beneficial for you in the coming weeks.";
  }
  
  if (query.toLowerCase().includes('money') || query.toLowerCase().includes('finance') || query.toLowerCase().includes('wealth')) {
    return "Your 2nd house in your birth chart shows potential for financial growth. Jupiter's position suggests expansion in financial matters, but Saturn reminds you to be disciplined with savings. The next quarter looks promising for investments.";
  }
  
  // Default to random response if no keywords match
  return FALLBACK_RESPONSES[Math.floor(Math.random() * FALLBACK_RESPONSES.length)];
};

const ChatPage = () => {
  const router = useRouter();
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [birthDetails, setBirthDetails] = useState<BirthDetails | null>(null);
  const [initializing, setInitializing] = useState(true);
  const [conversationId, setConversationId] = useState<string | null>(null);
  const [useBackend, setUseBackend] = useState(true); // Toggle for fallback mode
  const [backendAttempts, setBackendAttempts] = useState(0);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Check authentication and fetch birth details when component mounts
  useEffect(() => {
    const checkAuth = async () => {
      const token = localStorage.getItem('token');
      if (!token) {
        router.push('/auth/login');
        return;
      }

      try {
        // Get user profile with birth details
        const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/user/profile`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (!response.ok) {
          if (response.status === 401) {
            // Token expired or invalid
            localStorage.removeItem('token');
            router.push('/auth/login');
            return;
          } else if (response.status === 422) {
            console.warn('JWT processing error, trying to continue...');
            // We'll try to continue anyway but in fallback mode
            setUseBackend(false);
          } else {
            throw new Error('Failed to fetch user details');
          }
        } else {
          const userData = await response.json();
          
          // Check if birth details exist
          if (!userData.birth_details || !userData.birth_details.date) {
            router.push('/birth-details');
            return;
          }
          
          setBirthDetails(userData.birth_details);
        }
        
        // Initialize chat with welcome message
        await startConversation();
        
      } catch (err) {
        console.error('Error during initialization:', err);
        setError('Failed to initialize chat. Using offline mode.');
        setUseBackend(false);
        
        // Add welcome message even in fallback mode
        setMessages([{
          id: Date.now().toString(),
          content: WELCOME_MESSAGE,
          type: 'ai',
          timestamp: new Date()
        }]);
      } finally {
        setInitializing(false);
      }
    };

    checkAuth();
  }, [router]);

  // Auto-scroll to bottom when messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const startConversation = async () => {
    if (!useBackend) {
      // Fallback mode - add welcome message without API call
      setMessages([{
        id: Date.now().toString(),
        content: WELCOME_MESSAGE,
        type: 'ai',
        timestamp: new Date()
      }]);
      return;
    }
    
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/chat/start`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          birth_details: birthDetails
        })
      });

      if (!response.ok) {
        if (response.status === 422 || response.status >= 500) {
          // Backend not ready or server error - use fallback mode
          setUseBackend(false);
          throw new Error('Backend service unavailable. Using offline mode.');
        }
        throw new Error('Failed to start conversation');
      }

      const data = await response.json();
      
      // Save conversation ID for future messages
      setConversationId(data.conversation_id);
      
      // Add AI welcome message
      setMessages([{
        id: Date.now().toString(),
        content: data.initial_response || WELCOME_MESSAGE,
        type: 'ai',
        timestamp: new Date()
      }]);
      
    } catch (err: any) {
      console.error('Error starting conversation:', err);
      
      if (backendAttempts < 2) {
        // Try one more time
        setBackendAttempts(prev => prev + 1);
      } else {
        // Switch to fallback mode after multiple failures
        setUseBackend(false);
        
        // Add welcome message in fallback mode
        setMessages([{
          id: Date.now().toString(),
          content: WELCOME_MESSAGE,
          type: 'ai',
          timestamp: new Date()
        }]);
        
        // Clear error after showing message
        setTimeout(() => {
          setError('');
        }, 5000);
      }
    }
  };

  const handleSendMessage = async () => {
    if (!input.trim() || loading) return;
    
    const userMessage: Message = {
      id: Date.now().toString(),
      content: input,
      type: 'user',
      timestamp: new Date()
    };
    
    // Add a placeholder for the AI response
    const placeholderId = (Date.now() + 1).toString();
    
    setMessages(prev => [...prev, userMessage, {
      id: placeholderId,
      content: '',
      type: 'ai',
      timestamp: new Date(),
      isLoading: true
    }]);
    
    const userQuery = input;
    setInput('');
    setLoading(true);
    setError('');
    
    // Use fallback responses if backend is not available
    if (!useBackend) {
      // Wait a moment to simulate thinking
      setTimeout(() => {
        // Replace placeholder with fallback response
        setMessages(prev => 
          prev.map(msg => 
            msg.id === placeholderId ? {
              ...msg,
              content: getRandomResponse(userQuery),
              isLoading: false
            } : msg
          )
        );
        setLoading(false);
      }, 1500);
      return;
    }
    
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/chat/continue`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          message: userQuery,
          conversation_id: conversationId
        })
      });
      
      if (!response.ok) {
        // If backend returns errors, switch to fallback mode
        if (response.status === 422 || response.status >= 500) {
          setUseBackend(false);
          throw new Error('Backend service unavailable. Using offline mode.');
        }
        
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to get response');
      }
      
      const data = await response.json();
      
      // Replace placeholder with actual response
      setMessages(prev => 
        prev.map(msg => 
          msg.id === placeholderId ? {
            ...msg,
            content: data.response || "I understand your question. Let me provide some insights based on Vedic astrology principles.",
            isLoading: false
          } : msg
        )
      );
      
    } catch (err: any) {
      console.error('Error sending message:', err);
      
      // After errors, use fallback response without showing error
      setMessages(prev => 
        prev.map(msg => 
          msg.id === placeholderId ? {
            ...msg,
            content: getRandomResponse(userQuery),
            isLoading: false
          } : msg
        )
      );
      
      // Only show error briefly
      setError(err.message || 'Using offline mode for responses.');
      setTimeout(() => {
        setError('');
      }, 3000);
      
      // Switch to fallback mode after repeated errors
      setUseBackend(false);
      
    } finally {
      setLoading(false);
    }
  };

  // Handle Enter key press
  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  // Try to reconnect with backend
  const handleRetryBackend = async () => {
    setError('');
    setUseBackend(true);
    
    try {
      const token = localStorage.getItem('token');
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_BASE_URL}/api/chat/start`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          birth_details: birthDetails
        })
      });

      if (!response.ok) {
        throw new Error('Backend still unavailable. Continuing in offline mode.');
      }

      const data = await response.json();
      setConversationId(data.conversation_id);
      
      // Show success message
      setError('Successfully reconnected to backend service!');
      setTimeout(() => {
        setError('');
      }, 3000);
      
    } catch (err: any) {
      console.error('Error reconnecting to backend:', err);
      setUseBackend(false);
      setError('Could not connect to backend service. Continuing in offline mode.');
      
      setTimeout(() => {
        setError('');
      }, 3000);
    }
  };

  if (initializing) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center space-y-4">
          <Loader2 className="h-12 w-12 animate-spin mx-auto text-purple-600" />
          <p className="text-gray-600">Initializing your astrological consultation...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col h-screen bg-gray-50">
      {/* Header with birth details */}
      <div className="bg-white border-b border-gray-200 p-4">
        <div className="max-w-5xl mx-auto flex flex-col md:flex-row justify-between items-center">
          <div className="flex items-center mb-4 md:mb-0">
            <div className="bg-purple-100 p-2 rounded-full mr-3">
              <Sparkles className="h-6 w-6 text-purple-600" />
            </div>
            <h1 className="text-xl font-semibold">Astrological Consultation</h1>
            {!useBackend && (
              <span className="ml-2 text-xs bg-amber-100 text-amber-800 px-2 py-1 rounded-full">
                Offline Mode
              </span>
            )}
          </div>
          
          {birthDetails && (
            <div className="flex flex-wrap gap-3 text-sm text-gray-500">
              <div className="flex items-center">
                <Calendar className="h-4 w-4 mr-1" />
                <span>{birthDetails.date}</span>
              </div>
              <div className="flex items-center">
                <Clock className="h-4 w-4 mr-1" />
                <span>{birthDetails.time}</span>
              </div>
              <div className="flex items-center">
                <MapPin className="h-4 w-4 mr-1" />
                <span>{birthDetails.place}</span>
              </div>
              {!useBackend && (
                <Button 
                  variant="outline" 
                  size="sm" 
                  className="text-xs" 
                  onClick={handleRetryBackend}
                >
                  <RefreshCw className="h-3 w-3 mr-1" />
                  Reconnect
                </Button>
              )}
            </div>
          )}
        </div>
      </div>
      
      {/* Main chat area */}
      <div className="flex-1 overflow-hidden">
        <div className="max-w-5xl h-full mx-auto flex flex-col">
          <ScrollArea className="flex-1 p-4">
            <div className="space-y-6">
              {messages.map((message) => (
                <div
                  key={message.id}
                  className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                >
                  <div 
                    className={`flex items-start space-x-2 max-w-[80%] ${
                      message.type === 'user' ? 'flex-row-reverse space-x-reverse' : ''
                    }`}
                  >
                    <div className={`p-2 rounded-full ${
                      message.type === 'user' ? 'bg-purple-100' : 'bg-blue-100'
                    }`}>
                      {message.type === 'user' ? 
                        <User className="h-5 w-5 text-purple-600" /> : 
                        <Sparkles className="h-5 w-5 text-blue-600" />
                      }
                    </div>
                    <div 
                      className={`p-4 rounded-lg ${
                        message.type === 'user' 
                          ? 'bg-purple-600 text-white' 
                          : 'bg-white border border-gray-200'
                      }`}
                    >
                      {message.isLoading ? (
                        <div className="flex items-center space-x-2">
                          <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-0"></div>
                          <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-150"></div>
                          <div className="w-2 h-2 rounded-full bg-gray-300 animate-bounce delay-300"></div>
                        </div>
                      ) : (
                        <div className="prose" dangerouslySetInnerHTML={{ 
                          __html: message.content.replace(/\n/g, '<br>') 
                        }} />
                      )}
                    </div>
                  </div>
                </div>
              ))}
              <div ref={messagesEndRef} />
            </div>
          </ScrollArea>
          
          {/* Input area */}
          <div className="border-t border-gray-200 bg-white p-4">
            {error && (
              <Alert variant={error.includes('offline mode') ? "default" : "destructive"} className="mb-4">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}
            
            <div className="flex space-x-2">
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                onKeyDown={handleKeyPress}
                placeholder="Ask any question about your astrological chart..."
                disabled={loading || initializing}
                className="flex-1"
              />
              <Button 
                onClick={handleSendMessage}
                disabled={loading || initializing || !input.trim()}
                className="bg-purple-600 hover:bg-purple-700"
              >
                {loading ? (
                  <Loader2 className="h-5 w-5 animate-spin" />
                ) : (
                  <Send className="h-5 w-5" />
                )}
              </Button>
            </div>
            
            {/* Suggestion chips */}
            <div className="flex flex-wrap gap-2 mt-3">
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setInput("What does my birth chart reveal about my career path?")}
                className="text-xs"
              >
                <Lightbulb className="h-3 w-3 mr-1" />
                Career insights
              </Button>
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setInput("What planetary positions influence my relationships?")}
                className="text-xs"
              >
                <Lightbulb className="h-3 w-3 mr-1" />
                Relationship analysis
              </Button>
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setInput("What remedies would help balance my chart's challenges?")}
                className="text-xs"
              >
                <Lightbulb className="h-3 w-3 mr-1" />
                Remedial suggestions
              </Button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChatPage;